:root {
  --font-h:"Fraunces", sans-serif;
  --font-p:"Barlow", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --hero-white:hsl(43, 78%, 98%);
  --dark-cyan:hsl(179, 81%, 29%);
  --cyan-hover:hsl(178, 55%, 61%);
  --dark-grey-blue:	hsl(213, 17%, 21%);
  --pale-orange:	hsl(25, 94%, 86%);
  --txt-grey:hsl(215, 19%, 25%);
  --bg-order:hsl(0, 0%, 59%);
  --cream:hsl(40, 29%, 94%);
  --links:	hsl(215, 5%, 54%);
  --countries-color: hsla(216, 20%, 25%, 1);
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: var(--font-p);
  overflow-x: hidden;
  padding: 3.23rem 2.4rem 0 2.4rem;
}
@media (min-width: 48em) {
  body {
    padding: 4rem 3.9rem 0 3.9rem;
  }
}
@media (min-width: 90em) {
  body {
    padding: 4.36rem 8rem 0 8rem;
  }
}

li {
  list-style: none;
  cursor: pointer;
}

img,
picture {
  max-width: 100%;
}

.span_smoke {
  opacity: 1;
  animation: amin 1s;
}
@keyframes amin {
  0% {
    opacity: 0;
    transform: rotateY(-90deg);
    filter: blur(1rem);
  }
  100% {
    opacity: 1;
    transform: rotateY(0deg);
    filter: blur(0);
  }
}
.span_smoke:nth-child(1) {
  animation-delay: 1s;
}
.span_smoke:nth-child(2) {
  animation-delay: 1.5s;
}
.span_smoke:nth-child(3) {
  animation-delay: 2s;
}
.span_smoke:nth-child(4) {
  animation-delay: 2.5s;
}

video {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  left: 0;
  opacity: 0.6;
  border-radius: 1rem;
  object-fit: cover;
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
  font-family: var(--font-h);
}

/* Headings colours & fonts */
h1 {
  font-size: 4rem;
  line-height: 4rem;
}
@media (min-width: 48em) {
  h1 {
    font-size: 4.8rem;
    line-height: 4.8rem;
  }
}
@media (min-width: 90em) {
  h1 {
    font-size: 7.2rem;
    line-height: 7.2rem;
  }
}

h2 {
  font-size: 3.2rem;
  line-height: 4.8rem;
}
@media (min-width: 90em) {
  h2 {
    font-size: 4rem;
    line-height: 4.8rem;
  }
}

h3 {
  font-size: 2.8rem;
}
@media (min-width: 48em) {
  h3 {
    font-size: 3.2rem;
  }
}
@media (min-width: 90em) {
  h3 {
    font-size: 4rem;
  }
}

h4 {
  font-size: 2.4rem;
  line-height: 3.2rem;
}

.h-w {
  color: var(--hero-white);
}

.h-g {
  color: var(--txt-grey);
}

.h-l {
  line-height: clamp(2.8rem,5.195vw,4.8rem);
  font-weight: 900;
}

.p-g {
  color: var(--txt-grey);
}

.p-hw {
  color: var(--hero-white);
  font-style: normal;
  font-weight: normal;
}

.p-cpw {
  color: var(--white);
}

.p-sm {
  font-size: 1.5rem;
  line-height: 2.5rem;
}
@media (min-width: 90em) {
