    font-size: 1.6rem;
    line-height: 2.6rem;
  }
}

.p-lg {
  font-size: 1.6rem;
  line-height: 2.6rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--links);
  font-size: 1.2rem;
  line-height: 1.5rem;
  letter-spacing: 0.092rem;
  font-weight: bold;
  text-transform: uppercase;
  position: relative;
}
a:after,
a:visited:after,
a:active:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.3rem;
  bottom: 0;
  left: 0;
  background-color: var(--dark-cyan);
  transform: scaleX(0);
  transform-origin: bottom right;
  transition: transform 0.3s;
}
a:hover:after,
a:visited:hover:after,
a:active:hover:after {
  transform-origin: bottom left;
  transform: scaleX(1);
}

svg {
  cursor: pointer;
}

.shadow-text {
  filter: drop-shadow(0 0.4rem 0.4rem rgba(0, 0, 0, 0.5));
}

.pale_num {
  display: inline-block;
  font-size: 7.2rem;
  line-height: 7.2rem;
  font-weight: 900;
  font-family: var(--font-h);
  color: var(--pale-orange);
  margin-bottom: 2.4rem;
  position: relative;
  cursor: pointer;
}

@media (min-width: 40.25em) {
  .pale_num:before {
    cursor: pointer;
    position: absolute;
    width: 2.9rem;
    height: 2.9rem;
    content: "";
    border: 0.3rem solid #0e8784;
    top: -7rem;
    border-radius: 50%;
    left: 0;
    max-width: 79rem;
  }
}

.pale_num:hover::before {
  background-color: #0e8784;
  scale: 1.15;
}

@media (min-width: 40.25em) {
  .noline:after {
    position: absolute;
    width: 88%;
    height: 0.2rem;
    content: "";
    background-color: var(--pale-orange);
    top: -5.5rem;
    left: 3rem;
  }
}
@media screen and (min-width: 40.25em) and (min-width: 919px) {
  .noline:after {
    width: 90%;
  }
}
@media screen and (min-width: 40.25em) and (min-width: 1027px) {
  .noline:after {
    width: 92%;
  }
}

.hidden {
  display: none;
}

.rotate {
  transform: rotate(180deg);
  transition: 300ms linear all;
}

.container {
  margin: 0 auto;
}

.flex {
