  top: 6rem;
  bottom: 9rem;
  left: 0;
  right: 0;
  height: 60vh;
  padding-top: 4rem;
  background-color: aliceblue;
  background: #fefcf7;
  background: linear-gradient(180deg, #fefcf7 50.5%, rgba(0, 0, 0, 0.25) 100%);
  box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.25);
}
.header .overlay .overlay-wrapp {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3.2rem;
  animation: rotateMenu 700ms ease-in;
  transform-origin: top center;
